import React from 'react'
import axios from '../plugins/axios'
import {Route, Redirect} from 'react-router-dom';
export default class Auth extends React.Component{
    state={
        hasAuth:false,
        err:1,
        data:{},
    };
    componentDidMount(){
        axios(
            `${axios.baseUrl}/api/user`
        ).then(
            res=>this.setState({hasAuth:true,err:res.data.err,data:res.data.data})
        )
    }
    render(){
        let {component:Component,...rest} = this.props;
        if(!this.state.hasAuth) return null
        return <Route render={routeProps=>(
            this.state.err === 0 ?
            <Component { ...rest } {...routeProps } data={this.state.data} /> : //data = props
        <Redirect to="/login" />
        )}/>
    }
}