import React from 'react'
import {Link} from 'react-router-dom'
import '../assets/css/login.css'
import Input from '../components/input/Input'
import Button from '../components/button/Button'
import store from '../plugins/redux';
import {checkUser} from '../store/actions'
export default class Login extends React.Component{
    state={
        username:'',
        password:'',
        mess:''
    };
    render(){
        return (
           <div>
        <div className="content">
        <p className="fhbtn"><a onClick={()=>this.props.history.go(-1)}></a></p>
        <h1 id="h1"></h1>
        <div className="login-box">
          <p className="lsolid"></p>
          <div className="login">

            <Link to="/login"></Link>
            <span></span>
            <Link to="/reg"></Link>
          </div>
          <p className="rsolid"></p>
          </div>
        </div>
            <Input 
            name="username"
            type="text"
            label=""
            value = {this.state.username}
            onChange={this.changeIpt}
            />
            <Input
            name="password"
            type="password"
            label=""
            value={this.state.password} 
             onChange={this.changeIpt}

            />
            <Button value="" clickHander={this.login}/>
            </div>
            
        )
    }
    changeIpt=(ev)=>{
        this.setState({
            [ev.target.name]:ev.target.value
        })
    }
    login=()=>{
        store.dispatch(checkUser({
            api:'login',
            method:'post',
            username:this.state.username,
            password:this.state.password

        })).then(
            data=>{
                if(data.err===1){
                    this.setState({mess:data.msg})

                }else{
                    window.localStorage.setItem('react_news_app_uid',JSON.stringify(data));
                    this.props.history.push('/user')
                }
            }
        )
       
        
    }
}
