import React from 'react';
import {withRouter} from 'react-router-dom'
import guanzhu from '../assets/css/guanzhu.module.css'
import axios from '../plugins/axios'
class Guanzhu extends React.Component{
   follow=(id,dataName)=>{
      this.refs.r.style.backgroundColor = "gray";
      this.props.history.push({pathname:'/detail/'+id,search:'dataName='+dataName})

   }

    
   // btn=()=>{
   //    this.props.history.push("/home")
   // };
   
   render(){
     
     
      let {item,dataName} = this.props
      return (
         <div className={guanzhu.div}>
            <img src={axios.baseUrl + '/' + item.img} className={guanzhu.img}/>
            <span className={guanzhu.span}>{item.title}</span>
         <input type="button" value="" className={guanzhu.input} onClick={()=>this.follow(item._id,dataName)} ref="r"/>

         </div>
      )
      
   }
}
export default withRouter(Guanzhu)