import React from 'react'
import {NavLink} from 'react-router-dom'
import style from  '../assets/css/header.module.css'
export default class Header extends React.Component{
    render(){
        return (
            <div className={style.nav}>
            <ul>
              <li><NavLink to="/home" activeClassName={style.active}></NavLink></li>
              <li><NavLink to="/follow" activeClassName={style.active}></NavLink></li>
              <li><NavLink to="/column" activeClassName={style.active}></NavLink></li>
            </ul>
          </div>
        )
    }

}