import React from 'react';
import './Swiper.css';

import $ from 'jquery';
import Swipe from '../../library/swipe';

import { Link } from 'react-router-dom'

export default class Swiper extends React.Component{
    state={
        banner:[]
    };
    // constructor(props){
    //     super();
       
    // };

  componentDidMount(){
    new Swipe($('.banner')[0],{
      auto:2000,
      continuous:true,
      stopPropation:true,
      callback:function (index,element){
        $('.banner ol li').removeClass('active');
        $('.banner ol li').eq(index).addClass('active');
      }
    });
    // React.axios({
    //     url:'/mock/banner',
        
    // }).then(
    //     res=>this.setState({banner:res.data.data})
        
    // )


  }

  render(){
      let {banner} = this.state
    return(
      <div className="banner">
        <ul className="clearfix">
          <li>
            <Link to={{pathname:'/detail/2',search:'dataName=banner'}}>
              <img src="/images/a2.jpg" alt=""/>
              <div className="text-box">
                <h2>An Interesting UX Design Departure for Mechanical Wristwatches</h2>
                <p></p>
              </div>
            </Link>
          </li>
          <li>
            <Link to={{pathname:'/detail/2',search:'dataName=banner'}}>
              <img src="/images/a3.jpg" alt=""/>
              <div className="text-box">
                <h2>Who Knew? The Waterbed Was Invented by an Industrial Design</h2>
                <p></p>
              </div>
            </Link>
          </li>
          <li>
            <Link to={{pathname:'/detail/2',search:'dataName=banner'}}>
              <img src="/images/a4.jpg" alt=""/>
              <div className="text-box">
                <h2>The Digitally-Fabricated Version of a Map With Red Pins In It</h2>
                <p></p>
              </div>
            </Link>
          </li>
          {/* {
              banner.map(item=>(
                <li key={item.id}>
                <Link to="/detail/2">
                  <img src="/images/a2.jpg" alt=""/>
                  <div className="text-box">
                    <h2>{item.title}</h2>
                    <p>11 / </p>
                  </div>
                </Link>
              </li>
              ))
          } */}

        </ul>

      </div>
    )
  }
}