import React from 'react'
import {Carousel,Flex,Icon,SearchBar,Grid,WhiteSpace,Tabs,Badge} from 'antd-mobile'
import './shopcart.css';
import News from '../pages/News'
import { Link,Route} from 'react-router-dom';
import Follow from './Follow';
import Column from './Column'
export default class ShopCart extends React.Component {
    state = {
      data: ['a1', 'a2', 'a3'],
      imgHeight: 180,
      grids:[
        {id:1,icon:'/images/a1.jpg',text:''},
        {id:2,icon:'/images/a2.jpg',text:''},
        {id:3,icon:'/images/a3.jpg',text:''},
        {id:4,icon:'/images/a4.jpg',text:''},
        {id:5,icon:'/images/a5.jpg',text:''},
        {id:6,icon:'/images/a6.jpg',text:''},
        {id:7,icon:'/images/a7.jpg',text:''},
        {id:8,icon:'/images/a8.jpg',text:''}
      ],
      tabs: [
        { title: '' },
        { title: <Badge dot></Badge> },
        { title: '' },
      ],
    }
    
  
    render() {
      return (
        <div style={{paddingTop:44,paddingBottom:50}}>
          
          {/*  */}
          <Flex style={{position:"fixed",top:0,left:0,width:'100%',zIndex:999,textAlign:'center'}}>
            <Flex.Item style={{flex:0.2}}>LOGO</Flex.Item>
            <Flex.Item style={{flex:0.6}}>
              <SearchBar placeholder="Search" maxLength={8} />
            </Flex.Item>
            <Flex.Item style={{flex:0.2}}>
              <Icon type="ellipsis" size="lg"/>
            </Flex.Item>
          </Flex>
  
         {/*  */}
          <Carousel
            autoplay={true}
            infinite
          >
            {this.state.data.map(val => (
  
              <Link
                to={'/detail/' + val + '?dataName=banner'}
                key={val}
                style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
              >
                <img
                  alt=""
                  src={`/images/${val}.jpg`}
                  style={{ width: '100%', verticalAlign: 'top' }}
                />
              </Link>
  
            ))}
          </Carousel>
          
          {/*  */}
          <WhiteSpace/>
  
          {/*  */}
          <Grid
            data={this.state.grids}
            onClick={(el,index)=>console.log(el,index)}
          />
  
          <WhiteSpace/>
          
          {/* Tabs  */}
          <Tabs
            tabs={this.state.tabs}
            initialPage={1}
            tabBarUnderlineStyle={{borderColor:'#399'}}
            tabBarBackgroundColor="none"
            tabBarActiveTextColor="#399"
            onChange={(tab, index) => { console.log('onChange', index, tab); }}
            onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
          >
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
             <News/>
            </div>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
             <Column/>
            </div>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
              <Follow/>
            </div>
          </Tabs>
  
          
        </div>
      )
    }
  }