import React from 'react';
import '../assets/css/Reg.css';
import Button from '../components/button/Button';
import Input from '../components/input/Input';
import {Link} from 'react-router-dom';
import axios from '../plugins/axios'
export default class Reg extends React.Component{
    state={
        username:'',
        password:'',
        mess:''
    };
    reg=() => {
        axios({
            url:axios.baseUrl + `/api/reg`,
            method:'post',
            data:{
                username:this.state.username,
                password:this.state.password
            }
        }).then(
            res=>{
                if(res.data.err===1){
                    this.setState({mess:res.data.msg})

                }else{
                    alert('');
                    this.props.history.push('/login')
                }
            }
        )

    };
    changeIpt=(ev)=>{
        this.setState({
            [ev.target.name]:ev.target.value
        })

    }
    render(){
        return(
            <div className="content">
              <p className="fhbtn"><a onClick={()=>window.history.go(-1)}></a></p>
              <h1></h1>
              <div className="login-box">
                <p className="lsolid"></p>
                <div className="login">
                  <Link to="/login"></Link>
                  <span></span>
                  <Link to="/reg"></Link>
                </div>
                <p className="rsolid"></p>
              </div>
              <ul>
                <Input name="username" value={this.state.username} onChange={this.changeIpt} label=""/>
                <Input type="password" name="password" value={this.state.password} onChange={this.changeIpt} label=""/>
                <li>{this.state.mess}</li>
              </ul>
              <div className="footbox">
                <Button value="" clickHanlder={this.reg}/>
                <a href="#" className="tishi"></a>
              </div>
            </div>
          )

    }
}