import axios from '../plugins/axios';
import {CHECK_USER} from './types';
const update = ({api,_page=1,_limit=10,type,id=null}) =>dispatch=> (
    axios({
        url:id ? `${axios.baseUrl}/api/${api}/${id}` :`${axios.baseUrl}/api/${api}`,
        params:{_page,_limit}
    }).then(
        res=>{
            dispatch({type,payload:res.data.data});

        }
    ).catch(
        err=>new Error(err)
    )
);
const checkUser = ({api,method='get',username,password}) =>(dispatch,getState) =>(
    axios({
        url:`${axios.baseUrl}/api/${api}`,
        method,
        params:method === 'get' ? {username,password} :null,
        data:method === 'post' ? {username,password} :null,

    }).then(
        res=>{
            dispatch({type:CHECK_USER,payload:res.data});
            return res.data
        }
    )
);
export {update,checkUser};