import React from 'react';
import Footer from './Footer'
import Header from './Header'
import {Switch,Route,Redirect} from 'react-router-dom'
import Home from '../pages/Home'
import Follow from '../pages/Follow'
import Column from '../pages/Column'
import Detail from '../pages/Detail'
import User from '../pages/User'
import Login from '../pages/Login'
import Auth from '../guard/Auth'
import Reg from '../pages/Reg'
import ShopCart from '../pages/ShopCart'
import News from '../pages/News'
import {connect} from 'react-redux'
import * as types from '../store/types'
 class App extends React.Component{
    state={};
    static getDerivedStateFromProps(nextProps,nextState){
        let {viewNav,viewFoot,location:{pathname}} = nextProps;
        if(/home|follow|column/.test(pathname)){
            viewNav(true);
            viewFoot(true);
        }
        if(/detail|login|reg/.test(pathname)){
            viewNav(false);
            viewFoot(false);

        }if(/user|shopcart/.test(pathname)){
            viewNav(false)
            viewFoot(true)

        }
        return null

    }
    render(){
        let{bNav,bFoot,bLoding} = this.props;
        return(
            <div className="App">
                {/* {bLoding && <bLoding/>} */}
                {bNav && <Header/>}
                <Switch>
                    <Route path="/home" component={Home}/>
                    <Route path="/follow" component={Follow}/>
                    <Route path="/column" component={Column}/>
                    <Route path="/detail/:id" component={Detail}/>
                    <Auth path="/user" component={User}/>
                    <Route path="/login" component={Login}/>
                    <Route path="/reg" component={Reg}/>
                    <Route path="/shopcart" exact component={ShopCart}/>
                    <Route path="/shopcart/news" component={News}/>
                    <Redirect exact from="/" to="/home"/>
                </Switch>
               {bFoot ? <Footer/> :null} 

            </div>
        )
    }
}
const initMapStateToProps = state =>({
    bNav:state.bNav,
    bFoot:state.bFoot,
    bLoding:state.bLoding
});
const initMapDispatchToProps = dispatch =>({
    viewNav:(bl)=>dispatch({type:types.VIEW_NAV,payload:bl}),
    viewFoot:(bl)=>dispatch({type:types.VIEW_FOOT,payload:bl})
})
export default connect(initMapStateToProps,initMapDispatchToProps)(App)
