import React from 'react'
import GuanZhu from './GuanZhu'
import f from '../assets/css/Follow.module.css'
export default class Follow extends React.Component{
    state={
        follow:[]
    };
    async componentDidMount(){
        //.........
        let res = await React.axios({
          url:React.baseUrl + '/api/follow',
        //   params:{
        //     _page:1,
        //     _limit:8
        //   }
        }).then(
            res=>this.setState({follow:res.data.data})

        )
    
       
    }

     render(){
         console.log(this.state.follow)
        //  let {follow} = this.state
         return(

             <div className={f.follow}>
                 <h3>sdnbcihtr</h3>

                 
                 {
                    // this.state.follow.map(item=><div key={item.id}>{item.des}</div>)
                    //  <GuanZhu key={item.id} item={item} dataName='follow'/>
                    
                    this.state.follow.map(item=><GuanZhu key={item._id} item={item} dataName='follow'/>)

                //  )
                
                 
                 }
             </div>
         )
     }
}