import React from 'react';
import '../assets/css/Detail.css';
import xing from '../assets/img/xing.png'
import querystring from 'query-string'
import date from "../utils/date";
import fillzero from "../utils/fillzero";
export default class Detail extends React.Component{

  state={
    detail:{}
  };

  goback=()=>{
    this.props.history.go(-1)
  };

  componentDidMount(){
    let id=this.props.match.params.id;
    console.log(this.props);
    
    let dataName=querystring.parse(this.props.location.search).dataName;
    console.log(dataName);
    
    React.axios({
      url:React.baseUrl + `/api/${dataName}/${id}`,
    }).then(
      res=>this.setState({detail:res.data.data})
    )
  }

  render(){

    let {detail} = this.state;

    let mockStyleStr = '<span style="background:red">xxx</span>';

    let el=null;


    if (detail){
      el = (
        <div className="Detail">
          <div className="nav">
            <ul>
              <li className="l-btn" onClick={this.goback}></li>
            </ul>
          </div>
          <div className="content">
            <div className="header clear"><h2><img src={React.baseUrl + '/' + detail.img} alt=""/></h2><p></p></div>
            <div className="cont">
              <h3>{detail.title} / {fillzero(4)}</h3>
              <div className="time"><p>{date(detail.time)}<span><img src={require("../assets/img/zan.png")} alt=""/></span></p>
              </div>
              <div
                className="text-box"
                dangerouslySetInnerHTML={{__html:mockStyleStr + detail.content}}
              >
              </div>
            </div>
          </div>
          <div className="foot-btn">
            <ul>
              <li className="say"><a href="#">
                <i></i><span>0</span>
              </a></li>
              <li className="zan"><a href="#">
                <i></i><span>0</span>
              </a></li>
              <li className="xing"><a href="#">
                <i><img src={xing} alt=""/></i>
              </a></li>
              <li className="fx"><a href="#">
                <i><img src={require('../assets/img/fx.png')} alt=""/></i>
              </a></li>
            </ul>
          </div>
        </div>
      )
    } else {
      el = <div></div>
    }

    return el;

  }
}