import React from 'react'
import style from './cell.module.css'
import date from "../../utils/date";
import { withRouter, Link } from 'react-router-dom';
 class Cell extends React.Component{
    godetail = (id,dataName)=>{
        this.props.history.push({pathname:'/detail/'+id,search:'dataName='+dataName})

    }
    render(){
        let{item,dataName} = this.props;
        
        return(
            <div className={style.Cell}>
                <div className={style.div}>
                    <div className={style.img}><img src={React.baseUrl + '/' + item.img} className={style.image}/><span onClick={()=>this.godetail(item._id,dataName)}>{item.content}</span></div>
                    <p className={style.p}>{date(item.time)}</p>
                </div>

            </div>
        )
    }
}
export default withRouter(Cell)