import React from 'react';
import ReactDOM from 'react-dom';

import App from './layout/App';
import './filters/filter';
import './assets/css/base.css'
import {BrowserRouter,HashRouter,Route} from 'react-router-dom'
import './plugins/axios'
import './assets/css/base.css';
import './assets/js/font'
import store from './plugins/redux';
import {Provider} from 'react-redux'
React.baseUrl = 'http://139.224.221.23:3001';
// import Index from './mobx';
// import {Provider} from 'mobx-react'
React.Component.prototype.baseUrl = 'http://139.224.221.23:3001 '
let local = window.localStorage.getItem('react_news_app_uid');
if (local){
  store.dispatch({type:'CHECK_USER',payload:JSON.parse(local)})
}
ReactDOM.render(
<Provider store={store}>
<BrowserRouter>
<Route component={App}/>

</BrowserRouter>
</Provider>,

document.getElementById('root'));

