import fillzero from './fillzero';
export default (data)=>{
    let d = new Date()
    d.setTime(data);
    let year = d.getFullYear();
    let month = d.getMonth()+1;
    let date = d.getDate();
    let hours = d.getHours();
    let min = d.getMinutes();
    let sec = d.getSeconds();
    return `${year}${fillzero(month)}${fillzero(date)} ${fillzero(hours)}:${fillzero(min)}:${fillzero(sec)}`

}