import React from 'react';
import '../assets/css/column.module.css';
import Cell from "../components/cell/Cell";
import {update} from "../store/actions";
import * as types from "../store/types";
import connect from "react-redux/es/connect/connect";

class News extends React.Component{
  componentDidMount(){
    this.props.getColumn()
  }
  render(){
    return(
      <div className="Column">
        {
          this.props.list.map(item=>(
            <Cell index={false} key={item.id} item={item} dataName="column"/>
          ))
        }
      </div>
    )
  }
}

const initMapStateToProps=state=>({
  list:state.column,
});

const initMapDispatchToProps=dispatch=>({
  getColumn:()=>dispatch(update({
    api:'column',
    type:types.UPDATE_COLUMN
  }))
});

export default connect(initMapStateToProps,initMapDispatchToProps)(News)