import React from 'react';
import style from '../assets/css/User.module.css';
import store from '../plugins/redux';
import { blockStatement } from '@babel/types';
import axios from '../plugins/axios'
export default class User extends React.Component{
  logout=()=>{
    console.log('sacsd')
    axios({
      url:axios.baseUrl + '/api/logout',
      method:'delete'

    }).then(
      res=>{
        if(res.data.err===0){
          window.localStorage.removeItem('react_news_app_uid');
          store.dispatch({type:'CLEAR_USER'})

        }
      }
    )

  }

  render(){
   
    let {data} = this.props;
    return(
      <div className={style.content}>
        <div className={style.header}>
          <h2><img src={data.icon} alt=""/></h2>
          <div className={style['user-box']}>
            <a>{data.nikename}</a>
            <a onClick={this.logout} style={{display:'block',color:'black'}}></a>
          </div>
          <ul className={style.clear}>
            <li>
              <span>{data.follow}</span>
              <p></p>
            </li>
            <li>
              <span>{data.fans}</span>
              <p className={style.end}></p>
            </li>
          </ul>
        </div>
        <div className={style.docList}>
          <ul>
            <li className={style['gk-text']}>
              <i></i>
              <p></p>
              <b></b>
              <span>0</span>
            </li>
            <li className={style['mm-text']}>
              <i></i>
              <p></p>
              <b></b>
              <span>0</span>
            </li>
            <li className={style['cg-text']}>
              <i></i>
              <p></p>
              <b></b>
              <span>0</span>
            </li>
            <li className={style['sc-text']}>
              <i></i>
              <p></p>
              <b></b>
              <span>0</span>
            </li>
            <li className={style.my_cz}>
              <i></i>
              <p></p>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}